<script setup>
import { useAuthStore } from "@/stores/auth";
import Infaq from "@/api/Infaq";
import { onBeforeMount, ref } from "vue";

const data = ref()
const message = ref()
const user = ref()


function salin(text) {
    navigator.clipboard.writeText(text)
    alert('Alhamdulillah. No Rek sudah tersalin, silahkan di Paste ke aplikasi perbankan anda.')
}

async function hapus(id) {
    if (confirm('Konfirmasi hapus kategori')) {
        const res = await Infaq.destroy(id)
        message.value = res.message
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth'
        })

        setTimeout(() => {
            location.reload()
        }, 2000);
    }
}

onBeforeMount(async () => {

    const authStore = useAuthStore()
    user.value = authStore.user

    data.value = await Infaq.all()
})

</script>

<template>
    <h1>Infaq</h1>

    <v-divider class="mb-4"></v-divider>

    <v-alert type="success" class="my-3" closable close-label="Close Alert" v-if="message">
        {{ message }}
    </v-alert>

    <v-btn color="indigo" class="my-2" prepend-icon="mdi-plus" @click="$router.push('/add-infaq')" v-if="user">
        Tambah Kategori
    </v-btn>

    <v-row>
        <v-col v-for="item in data" :key="item._id" cols="12" sm="4">
            <v-card class="mx-auto" max-width="400">
                <v-img class="align-end text-white" height="200" :src="'/' + item.gambar" cover>
                    <v-card-title>{{ item.judul }}</v-card-title>

                    <template v-slot:placeholder>
                        <div class="d-flex align-center justify-center fill-height">
                            <v-progress-circular indeterminate color="grey-lighten-4"></v-progress-circular>
                        </div>
                    </template>
                </v-img>

                <v-card-text>
                    <div>{{ item.deskripsi.substring(0, 80) + ' ...' }}</div>
                    <v-btn size="x-small" color="warning" class="mt-2" :to="'/find-infaq/' + item._id">
                        More info...
                    </v-btn>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-text>
                    <div>Bank: <strong>{{ item.bank }}</strong></div>
                    <div>No Rekening: <strong>{{ item.rekening }}</strong></div>
                    <div class="d-flex justify-space-between">
                        <v-btn variant="outlined" color="blue mt-3" prepend-icon="mdi-content-copy"
                            @click="salin(item.rekening)">
                            Salin
                        </v-btn>
                        <v-btn color="green mt-3" :to="'/payment/' + item._id">
                            Infaq Sekarang
                        </v-btn>
                    </div>
                </v-card-text>

                <template v-if="user">
                    <v-divider></v-divider>

                    <v-card-actions>
                        <v-btn color="green" :to="`/edit-infaq/${item._id}`">Edit</v-btn>
                        <v-btn color="red" @click="hapus(item._id)">Hapus</v-btn>
                    </v-card-actions>
                </template>
            </v-card>
        </v-col>
    </v-row>
</template>